import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/carpeta.jpg'
import thumb02 from '../assets/images/thumbs_ML/carpeta 2.jpg'
import thumb03 from '../assets/images/thumbs_ML/carpeta 3.jpg'
import thumb04 from '../assets/images/thumbs_ML/carpeta 4.jpg'

import full01 from '../assets/images/fulls/carpeta.jpg'
import full02 from '../assets/images/fulls/carpeta 2.jpg'
import full03 from '../assets/images/fulls/carpeta 3.jpg'
import full04 from '../assets/images/fulls/carpeta 4.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '4', src: full04, thumbnail: thumb04, caption: 'Photo 4', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Carpeta Escolar</h2>
                            <h4>$220</h4> 
                        </header>
                        <p>DESCRIPCION:  <br />
                        * Carpeta escolar con cierre.<br />
                        Se puede personalizar con cualquier imagen.</p>
                       
                       <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-3564f226-62bc-403d-9770-aee02e56408d" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br />
                        <Link to="/">Volver</Link>
                        <br /><br />

                    </section>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex

/*export const pageQuery = graphql
    query PageQuery {
        site {
            siteMetadata {
                title
                description
            }
        }
    }*/



